.App {
  text-align: center;
  background-color: #131316;
  color: #ffffff
}

section {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.hidden {
  opacity: 0;
  filter: blur(0);
  transform: translateX(0);
}

.show {
  opacity: 1;
  filter: blur(5px);
  transform: translateX(-100%);
  transition: all 1s;
}

.logos {
  display: flex;
}

.logo {
  height: 40px;
}

.logo:nth-child(2) {
  transition-delay: 200ms;
}
.logo:nth-child(3) {
  transition-delay: 300ms;
}
.logo:nth-child(4) {
  transition-delay: 400ms;
}
